<!-- eslint-disable vue/no-multiple-template-root -->
<template>
  <Header />

  <h1>Hello User, Welcome on Update Expenses Page</h1>
   <form class="add">
    <input type="text" name="expenseType" placeholder="Enter ExpenseType" v-model="expenses.expenseType" />
    <input type="text" name="date" placeholder="Enter date" v-model="expenses.date" />
    <input type="text" name="expenseRefNo" placeholder="Enter expenseRefNo" v-model="expenses.expenseRefNo" />
    <input type="text" name="particulars" placeholder="Enter particulars" v-model="expenses.particulars" />
    <input type="text" name="amount" placeholder="Enter amount" v-model="expenses.amount" />
    <input type="text" name="createdBy" placeholder="Enter createdBy" v-model="expenses.createdBy" />
    <input type="text" name="__v" placeholder="Enter __v" v-model="expenses.__v" />
    <button type="button" v-on:click="updateExpenses">Update Expenses</button>
    </form>
</template>

<script>
import Header from './Header.vue'
import axios from 'axios';
export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: "Update",
  components: {
    // eslint-disable-next-line vue/no-reserved-component-names
    Header
  },
   data()
    {
        return {
          expenses :{
            expenseType:'',
            date:'',
            expenseRefNo:'',
            particulars:'',
            amount:'',
            createdBy:'',
            __v:''

          }
        }
    },
  methods: {
    async updateExpenses() 
    {
      console.warn(this.expenses);
      const result = await axios.put("http://localhost:3000/expenses/"+this.$route.params.id,{
          expenseType:this.expenses.expenseType,
          date:this.expenses.date,
          expenseRefNo:this.expenses.expenseRefNo,
          particulars:this.expenses.particulars,
          amount:this.expenses.amount,
          createdBy:this.expenses.createdBy,
           __v:this.expenses.__v,
        });
        if(result.status==200)
        {
          this.$router.push({name:'Home'}) ;
        }
    }
  },
  async mounted() {
    let user = localStorage.getItem("user-info");
    if (!user) 
    {
      this.$router.push({ name: "SignUp" });
    }
    const result = await axios.get("http://localhost:3000/expenses/"+this.$route.params.id);
    // console.warn(this.$route.params.id)
    console.warn(result.data)
    this.expenses=result.data
  }
};
</script>

