<!-- eslint-disable vue/no-multiple-template-root -->
<template>

<Header />
  
  <h1>Hello {{name}}, Welcome on Home Page</h1>
  <table border="1">
    <tr>
      <td>Id</td>
      <td>Name</td>
      <td>Date</td>
      <td>ExpenseRefNo</td>
      <td>Particulars</td>
      <td>Amount</td>
      <td>CreatedBy</td>
      <td>__V</td>
      <td>Actions</td>
      
      </tr>
    <tr v-for="item in expenses" :key="item.id">
      <td>{{item.id}}</td>
      <td>{{item.expenseType}}</td>
      <td>{{item.date}}</td>
      <td>{{item.expenseRefNo}}</td>
      <td>{{item.particulars}}</td>
      <td>{{item.amount}}</td>
      <td>{{item.createdBy}}</td>
      <td>{{item.__v}}</td>
      <td><router-link :to="'/update/'+item.id">Update</router-link></td>
      <button v-on:click="deleteExpenses(item.id)">Delete</button>
  
      </tr>
    </table>
</template>

<script>
import Header from './Header.vue'
import axios from 'axios';
export default {
    // eslint-disable-next-line vue/multi-word-component-names
    name:'Home',
    data(){
      return {
        name:'',
        expenses:[],
      }
    },
    components:{
      // eslint-disable-next-line vue/no-reserved-component-names
      Header
    },
    methods:{
       async deleteExpenses(id)
        {
          let result = await axios.delete("http://localhost:3000/expenses/"+id);
          console.warn(result)
          if(result.status==200)
          {
             this.loadData()
          }
        },
        async loadData()
        {
          let user= localStorage.getItem('user-info');
      this.name= JSON.parse(user).name;
      if(!user)
      {
        this.$router.push({name:'SignUp'})
      }
      let result =await axios.get("http://localhost:3000/expenses");
      console.warn(result)
      this.expenses=result.data;
        }
    },

    async  mounted()
    {
      this.loadData();
    }
}
</script>

<style>
td{
  width:160px;
  height:40px
}
</style>